import { useState } from "react";
import { Form, ButtonToolbar, Button, DatePicker } from "rsuite";
import { createJob } from "../../../api/jobs";

export default function IncreaseForm(props) {
  const [formValue, setFormValue] = useState({ queue: "increase" });
  const [response, setResponse] = useState({});

  function onChange(vals) {
    setFormValue(vals);
  }

  function onSelectedDate(date) {
    setFormValue({ ...formValue, scheduled_at: date });
  }

  async function onSubmit() {
    const job = await createJob(formValue);
    setResponse(job);
  }
  return (
    <>
      <h3>Create increase job</h3>
      <Form onChange={(v) => onChange({ ...formValue, ...v })}>
        <Form.Group controlId="id" style={{ marginTop: 40 }}>
          <Form.ControlLabel>ID</Form.ControlLabel>
          <Form.Control name="id" autoComplete="off" />
        </Form.Group>
        <Form.Group controlId="number">
          <Form.ControlLabel>Number</Form.ControlLabel>
          <Form.Control name="number" autoComplete="off" />
        </Form.Group>
        <Form.Group>
          <DatePicker
            format="yyyy-MM-dd HH:mm:ss"
            ranges={[
              {
                label: "Now",
                value: new Date(),
              },
            ]}
            onSelect={(date) => onSelectedDate(date)}
            defaultValue={new Date()}
          />
        </Form.Group>
        <Form.Group>
          <ButtonToolbar>
            <Button appearance="primary" onClick={() => onSubmit()}>
              Submit
            </Button>
          </ButtonToolbar>
        </Form.Group>
      </Form>
      <p style={{ marginTop: 40 }}>
        <code>{JSON.stringify(response)}</code>
      </p>
    </>
  );
}
